/**
 * Privacy Policy Page JavaScript
 *
 * This file contains the JavaScript for the Privacy Policy page.
 */

// Import components
import Accordion from '../components/accordion';

/**
 * Initialize the page
 */
function initPage() {
  console.log('Privacy Policy page initialized');
  
  // Initialize accordion if there are any on the page
  Accordion.init({
    triggerSelector: '.policy-question',
    contentSelector: '.policy-answer',
    closeOthers: true,
    animateHeight: true
  });
  
  // Set up table of contents if it exists
  const tocContainer = document.getElementById('policy-toc');
  if (tocContainer) {
    generateTableOfContents(tocContainer);
  }
}

/**
 * Generate table of contents from policy section headers
 * @param {HTMLElement} container - Container for the table of contents
 */
function generateTableOfContents(container) {
  const sections = document.querySelectorAll('.policy-section h2');
  
  if (sections.length > 0) {
    const list = document.createElement('ul');
    
    sections.forEach((section, index) => {
      // Add ID to section if it doesn't have one
      if (!section.id) {
        section.id = 'policy-section-' + (index + 1);
      }
      
      // Create list item
      const item = document.createElement('li');
      const link = document.createElement('a');
      link.href = '#' + section.id;
      link.textContent = section.textContent;
      
      // Add click event to scroll smoothly
      link.addEventListener('click', function(e) {
        e.preventDefault();
        document.querySelector(this.getAttribute('href')).scrollIntoView({
          behavior: 'smooth'
        });
      });
      
      item.appendChild(link);
      list.appendChild(item);
    });
    
    container.appendChild(list);
  }
}

// Initialize when DOM is ready
document.addEventListener('DOMContentLoaded', initPage);

export default {
  initPage
};
